html {
  box-sizing: border-box;
  height: 100%;
  margin: 0;
  padding: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  box-sizing: border-box;
}

.master-content {
  flex: 1;
}

.error {
  background-color: #ff0000;
  color: #ffffff;
  padding: 2px 5px;
  margin-top: 5px;
  text-align: left;
  font-size: 0.8rem;
  position: absolute;
}

mark {
  background: transparent;
  position: relative;
  font-weight: 500;
  z-index: 1;
  display: inline-block;
  overflow: hidden;
  vertical-align: top;
  color: currentcolor;
}
mark::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background-color: #ecc452;
  z-index: -1;
}
mark.iniciar-animacao {
  animation: destacarTextoAnim 0.7s ease-out 1 forwards;
}
mark.iniciar-animacao::before {
  animation: destacarAnim 0.7s ease-out 1 forwards;
}

@keyframes destacarAnim {
  0% {
    left: -100%;
  }
  100% {
    left: 0;
  }
}
@keyframes destacarTextoAnim {
  0% {
    color: currentcolor;
  }
  100% {
    color: #252320;
  }
}
.header {
  width: 100%;
  height: 100px;
  background-color: #ecc452;
  display: flex;
  justify-content: center;
  align-content: center;
}
@media only screen and (max-width: 600px) {
  .header {
    justify-content: center;
    align-content: center;
  }
}

.logo {
  display: flex;
  width: 20vw;
  height: 100px;
  padding: 0.89vw 2vw;
  justify-content: center;
  align-content: center;
}
@media only screen and (max-width: 600px) {
  .logo {
    width: 75vw;
    height: auto;
  }
}

.logo img {
  width: 100%;
  object-fit: contain;
  margin-top: 15px;
}

nav {
  width: 67vw;
  height: 100px;
}
@media only screen and (max-width: 600px) {
  nav {
    display: none;
  }
}

ul.menu {
  height: 100px;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
}

ul.menu li {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-content: center;
  align-items: center;
  border-left: 1px solid #ffec78;
  margin: 0;
}

ul.menu li:last-child {
  border-right: 1px solid #ffec78;
}

ul.menu li a {
  display: flex;
  font-family: "Bebas Neue", sans-serif;
  color: #252320;
  font-size: 1.1rem;
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: none;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

ul.menu li a:hover {
  background-color: #caa431;
  background-image: linear-gradient(#ffe773, #ecc452);
}

.reserva {
  width: 13vw;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 600px) {
  .reserva {
    display: none;
    width: 0;
    height: 0;
  }
}

.reserva a {
  width: 90%;
  height: 70%;
  display: block;
  border: 3px solid #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000000;
  text-decoration: none;
  font-family: "Bebas Neue", sans-serif;
  font-size: 1.25rem;
}

.reserva a:hover {
  background-color: #000000;
  color: #ffffff;
  transition: 0.5s;
}

.topnav {
  display: none;
}
@media only screen and (max-width: 600px) {
  .topnav {
    display: block;
    background-color: #ecc452;
    height: 60px;
  }
}
@media only screen and (max-width: 600px) {
  .topnav a {
    float: left;
    display: block;
    color: #252320;
    text-align: center;
    padding: 15px;
    text-decoration: none;
    border-bottom: 1px solid #ffec78;
    font-weight: 500;
    font-size: 1.25rem;
    text-transform: uppercase;
    font-family: "Bebas Neue", sans-serif;
  }
}
@media only screen and (max-width: 600px) {
  .topnav a:first-child {
    border: none;
  }
}
@media only screen and (max-width: 600px) {
  .topnav a:hover {
    color: #ffffff;
  }
}

@media only screen and (max-width: 600px) {
  .topnav .icon {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  .topnav a:not(:first-child) {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  .topnav a.icon {
    position: absolute;
    right: 0;
    top: 0;
    float: right;
    display: block;
    border: none;
  }
}

@media only screen and (max-width: 600px) {
  .topnav.responsive {
    position: relative;
    height: auto;
    display: flex;
    flex-direction: column;
  }
}

@media only screen and (max-width: 600px) {
  .topnav.responsive a.icon {
    position: absolute;
    right: 0;
    top: 0;
  }
}

@media only screen and (max-width: 600px) {
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: center;
  }
}

